import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import TimelineOppositeContent, {
  timelineOppositeContentClasses,
} from '@mui/lab/TimelineOppositeContent';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { Button, Tooltip } from '@mui/material';

const KaratedoAbout = () => {
  const answerStyle = {
    paddingLeft: '1em',
    textIndent: '-1em',
    paddingBottom: '1em',
  };

  const [wikipedia, setWikipedia] = useState<any>(null);

  useEffect(() => {
    axios
      .get('https://ja.wikipedia.org/api/rest_v1/page/summary/%E7%B3%B8%E6%9D%B1%E6%B5%81')
      .then((response) => {
        setWikipedia(response.data.extract);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <h4></h4>
      
      <li style={answerStyle}></li>
      <li style={answerStyle}></li>
      <h4></h4>
      <Tooltip title={wikipedia + 'Wikipedia'} arrow>
        <Button href="https://ja.wikipedia.org/wiki/%E7%B3%B8%E6%9D%B1%E6%B5%81"></Button>
      </Tooltip>
      <h4></h4>
      <Timeline
        sx={{
          [`& .${timelineOppositeContentClasses.root}`]: {
            flex: 0.2,
          },
        }}
      >
        <TimelineItem>
          <TimelineOppositeContent color="text.success">1948</TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="success" />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            ()56
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent color="text.success">1949</TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="success" />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent> </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent color="text.secondary">1955</TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="secondary" />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent></TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent color="text.secondary">2005</TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="secondary" />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent></TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent color="text.secondary">2022</TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot variant="outlined" color="primary" />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent> </TimelineContent>
        </TimelineItem>
      </Timeline>
    </>
  );
};

export default KaratedoAbout;
